.profile {
    display: flex;
    flex-direction: column;
    width: 300px;
    border: 1px solid rgb(230, 228, 223);
    border-radius: 1%;
    padding-top: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.profile:hover,
.avatar:hover {
box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px,
        rgb(0 0 0 / 14%) 0px 3px 4px 0px,
        rgb(0 0 0 / 12%) 0px 1px 8px 0px;
    
}

.description {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    
}

.avatar {
    width: 120px;
    border-radius: 50%;
    border: 1px solid rgb(218, 218, 218);
    background-color: brown;
    margin-bottom: 20px;
}

.name,
.quantity {
    font-weight: 600;
    font-size: 24px;
    font-family: 'Roboto';
}

.tag,
.location {
    color: grey;
    font-weight: 300;
    font-size: 18px;
    font-family: 'Oxygen';
}

.stats{
    display: flex;
    background-color: rgb(227, 243, 243);
}

.stat{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid rgb(218, 218, 218);
    border-bottom: 0;
    width: 100%;
}

.stat:first-child{
    border-left: 0;
}
.stat:last-child{
    border-right: 0;
}

.label{
    display: inline-block; 
    color: grey;
    font-weight: 300;
    font-size: 18px;
    font-family: 'Oxygen';
    align-items: center;
}



